import React, { Component, PropTypes } from 'react'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import {platform} from 'onsenui';

import * as Actions from '../actions';

import { Page, ProgressCircular, Button, Input } from 'react-onsenui';

import NavBar from '../components/NavBar';

const styles = {
  main: {
    fontFamily: platform.isIOS() ? 'Lato' : null,
    textAlign: 'center',
    color: '#4a4a4a',
    width: '100%',
    marginTop: '30px'
  },
  name: {
    textTransform: 'uppercase',
    fontSize: '24px',
    lineHeight: '24px'
  }
};

const ViolationDetailPage = ({
  navigator, violationListReducer, violationDetailReducer, actions
}) => {
  let violationDetailContent;
  violationDetailContent = (
    <div>
      <div style={styles.name}>
        <div style={{margin: '20px'}}>
          <h3>ViolationDetail</h3>
          <h4>{violationListReducer.selectedViolationRule.title}</h4>
          <h5>{violationListReducer.selectedViolationRule.description}</h5>
          <br/>
        </div>
      </div>
    </div>
  )

  return (
    <Page renderToolbar={() => <NavBar backButton={true} title={violationListReducer.selectedViolationRule.title} navigator={navigator} />}>
      <div style={styles.main}>
        {violationDetailContent}
      </div>
    </Page>
  );
};








const mapStateToProps = (state) => ({
  ...state
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(Actions, dispatch)
  };
};

export default connect( mapStateToProps, mapDispatchToProps )(ViolationDetailPage);



