import {OPEN_DRAWER, CLOSE_DRAWER} from '../actions';

const drawer = (state = {open: false}, action) => {
  switch (action.type) {
    case OPEN_DRAWER:
      return {
        open: true
      };
    case CLOSE_DRAWER:
      return {
        open: false
      };
    default:
      return state;
  }
};

export default drawer;



