import {SET_ATTRIBUTE} from '../actions';

let initialState = {settings : {}, violationRules : []};

const violationListReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_ATTRIBUTE:
      switch (action.payload.attribute){
        case "VIOLATIONLIST_VIOLATIONRULES":
        return {...state, violationRules : action.payload.value};
        break;
        case "VIOLATIONLIST_SELECTVIOLATIONRULE":
        return {...state, selectedViolationRule : action.payload.value};
        break;
      }
    default:
      return state;
  }
};

export default violationListReducer;



