
/* FIREBASE LOGIN */
/*    ACTIONS  -- Firebase Page Start  */
export const SET_ATTRIBUTE = 'SET_ATTRIBUTE';
export const RECEIVE_SIGNUP = 'RECEIVE_SIGNUP';
export const RECEIVE_SIGNIN = 'RECEIVE_SIGNIN';
export const RECEIVE_SIGNOUT = 'RECEIVE_SIGNOUT';
export const RECEIVE_RESETPASSWORD = 'RECEIVE_RESETPASSWORD';

export const setAttribute = (payload) => ({
  type: SET_ATTRIBUTE,
  payload
});

export const receiveSignUp = (payload) => ({
  type: RECEIVE_SIGNUP,
  payload
});

export const signUp = () => {
  return (dispatch, getState) => {
    let state = getState();

    firebase.auth().createUserWithEmailAndPassword(state.newPageReducer.signUpUser.eMail, state.newPageReducer.signUpUser.password)
    .catch(function(error) {
      var errorCode = error.code;
      var errorMessage = error.message;
      if (errorCode == 'auth/weak-password') {
        alert('The password is too weak.');
      } else {
        alert(errorMessage);
      }
    }).then(function(resp){
        dispatch(receiveSignUp(resp));
        console.log("signed in");
      });
  };
};

export const logoutUser = () => {
  return (dispatch, getState) => {
    firebase.auth().signOut().then(function(resp){
                                     dispatch(receiveSignOut(resp));
                                     console.log("signed out");
                                   });
  };
};

export const receiveSignOut = (payload) => ({
  type: RECEIVE_SIGNOUT,
  payload
});

export const receiveSignIn = (payload) => ({
  type: RECEIVE_SIGNIN,
  payload
});

export const signIn = () => {
    console.log("Signing In");
  return (dispatch, getState) => {
    let state = getState();
    firebase.auth().signInWithEmailAndPassword(state.newPageReducer.signInUser.eMail, state.newPageReducer.signInUser.password)
    .catch(function(error) {
      var errorCode = error.code;
      var errorMessage = error.message;
      if (errorCode == 'auth/weak-password') {
        alert('The password is too weak.');
      } else {
        alert(errorMessage);
      }
    })
    .then(function(resp){
      dispatch(receiveSignIn(resp));
      console.log("signed in");
    });
  };
};

export const receiveResetPassword = (payload) => ({
  type: RECEIVE_RESETPASSWORD,
  payload
});

export const resetPassword = () => {
  return (dispatch, getState) => {
    let state = getState();
    firebase.auth().sendPasswordResetEmail(state.newPageReducer.resetPassword.eMail).then(function() {
      alert('Password Reset Email Sent!');
    }).catch(function(error) {
      var errorCode = error.code;
      var errorMessage = error.message;
      if (errorCode == 'auth/invalid-email') {
        alert(errorMessage);
      } else if (errorCode == 'auth/user-not-found') {
        alert(errorMessage);
      }
    }).then(function(resp){
            dispatch(receiveResetPassword(resp));
            console.log("signed in");
          });
  };
};

/*    ACTIONS  -- Firebase Page End  */




