import {combineReducers} from 'redux';
import drawer from './drawer';
import firebaseReducer from './firebaseReducer';
import homeReducer from './homeReducer';
import issueDetailReducer from './issueDetailReducer';
import issueListReducer from './issueListReducer';
import kidSearchReducer from './kidSearchReducer';
import preferencesReducer from './preferencesReducer';
import profileReducer from './profileReducer';
import tipDetailReducer from './tipDetailReducer';
import tipListReducer from './tipListReducer';
import violationDetailReducer from './violationDetailReducer';
import violationListReducer from './violationListReducer';


const orchardApp = combineReducers({
  drawer,
  firebaseReducer, homeReducer, issueDetailReducer, issueListReducer, kidSearchReducer, preferencesReducer, profileReducer, tipDetailReducer
  , tipListReducer, violationDetailReducer, violationListReducer
});

export default orchardApp;



