import React, { Component, PropTypes } from 'react'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {platform} from 'onsenui';
import * as Actions from '../actions';
import { Page, ProgressCircular, Button, Input } from 'react-onsenui';
import NavBar from '../components/NavBar';

import ViolationDetailPage from './ViolationDetailPage';

const styles = {
  main: {
    fontFamily: platform.isIOS() ? 'Lato' : null,
    textAlign: 'center',
    color: '#4a4a4a',
    width: '100%',
    marginTop: '30px'
  },
  name: {
    textTransform: 'uppercase',
    fontSize: '24px',
    lineHeight: '24px'
  }
};

const ViolationListPage = ({
  navigator, violationListReducer, actions
}) => {
  let violationListContent;
  violationListContent = (
    <div>
      <div style={styles.name}>
        <div style={{margin: '20px 0px'}}>
          <ul className="list list--material">
          {violationListReducer.violationRules.map(function(vr, index){
            return (<li key={index} onClick={() => {  actions.setAttribute({"attribute" : "VIOLATIONLIST_SELECTVIOLATIONRULE", "value" : vr});
                                          navigator.pushPage({component: ViolationDetailPage});}}
                        className="list__item list__item--material list__item--tappable  list__item--chevron">
                      <div className="list__item__center list__item--material__center">
                        <div className="list__item__title list__item--material__title">{vr.title}</div>
                        <div className="list__item__subtitle list__item--material__subtitle">{vr.description}</div>
                      </div>
                    </li>)
          })}
          </ul>
        </div>
      </div>
    </div>
  )

  return (
    <Page renderToolbar={() => <NavBar backButton={true} title="HOA Violation Types" navigator={navigator} />}>
      <div style={styles.main}>
        {violationListContent}
      </div>
    </Page>
  );
};








const mapStateToProps = (state) => ({
  ...state
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(Actions, dispatch)
  };
};

export default connect( mapStateToProps, mapDispatchToProps )(ViolationListPage);



