import React, { Component, PropTypes } from 'react'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import {platform} from 'onsenui';

import * as Actions from '../actions';

import { Page, ProgressCircular, Button, Input } from 'react-onsenui';

import NavBar from '../components/NavBar';

const styles = {
  main: {
    fontFamily: platform.isIOS() ? 'Lato' : null,
    textAlign: 'center',
    color: '#4a4a4a',
    width: '100%',
    marginTop: '30px'
  },
  name: {
    textTransform: 'uppercase',
    fontSize: '24px',
    lineHeight: '24px'
  }
};

const FirebasePage = ({
  navigator, firebaseReducer, actions
}) => {

/*  if(firebase.auth().currentUser){
    actions.setAttribute({"attribute" : "LOGIN_SCREENMODE", "value" : "SIGNEDIN"});
  }*/

  let userProfile = (
    <div>
      {firebase.auth().currentUser && <h4>
        Welcome {JSON.stringify(firebase.auth().currentUser.email)}
      </h4>}
    </div>
  )

  let loginContent, createAccountContent, forgotPasswordContent, logoutContent;
  loginContent = (
    <div>
      {firebaseReducer.screenMode === "SIGNIN" && <div style={styles.name}>
        <div style={{margin: '20px'}}>
          <h3>Sign In</h3><br/>
          <div className="box-1">
            <Input style={{"width" : "100%"}} placeholder='Email' modifier="material" float
              value={firebaseReducer.signInUser.eMail} onChange={(evt) => actions.setAttribute({"attribute" : "SIGNIN_EMAIL", "value" : evt.target.value})} />
            <br/>
            <br/>
            <Input style={{"width" : "100%"}} placeholder='Password' modifier="material" float type="password"
              value={firebaseReducer.signInUser.password} onChange={(evt) => actions.setAttribute({"attribute" : "SIGNIN_PASSWORD", "value" : evt.target.value})} />
            <br/>
            <br/>
            <br/>
            <Button  modifier='large' onClick={() => { actions.signIn(); }}  ripple={true}>Sign In</Button>
            <br/>
            <br/>
            <div className="link text-right" onClick={() => actions.setAttribute({"attribute" : "LOGIN_SCREENMODE", "value" : "FORGOT_PASSWORD"})}>Forgot Password?</div>
          </div>
          <br/>
          <div className="link" onClick={() => actions.setAttribute({"attribute" : "LOGIN_SCREENMODE", "value" : "SIGNUP"})}>Create New Account</div>
        </div>
      </div>}
    </div>
  )
  logoutContent = (
    <div>
      {firebaseReducer.screenMode === "SIGNEDIN" && <div style={styles.name}>
        <div style={{margin: '20px'}}>
          <h3>Log Out</h3><br/>
          <div className="box-1">
            <br/>
            <br/>
            <Button  modifier='large' onClick={() => { actions.logoutUser(); }}  ripple={true}>Sign Out</Button>
            <br/>
            <br/>
          </div>
        </div>
      </div>}
    </div>
  )
  forgotPasswordContent = (
    <div>
      {firebaseReducer.screenMode === "FORGOT_PASSWORD" && <div style={styles.name}>
        <div style={{margin: '20px'}}>
          <h3>Reset Password</h3><br/>
           <div className="box-1">
             <Input style={{"width" : "100%"}} placeholder='Email' modifier="material" float
              value={firebaseReducer.resetPassword.eMail} onChange={(evt) => actions.setAttribute({"attribute" : "RESETPASSWORD_EMAIL", "value" : evt.target.value})} />
             <br/>
             <br/>
             <br/>
             <Button  modifier='large' onClick={() => { actions.resetPassword(); }}  ripple={true}>Reset Password</Button>
           </div>
          <br/>
          <div className="link" onClick={() => actions.setAttribute({"attribute" : "LOGIN_SCREENMODE", "value" : "SIGNIN"})}>Sign In</div>
        </div>
      </div>}
    </div>
  )
  createAccountContent = (
    <div>
      {firebaseReducer.screenMode === "SIGNUP" && <div style={styles.name}>
        <div style={{margin: '20px'}}>
          <h3>Sign Up</h3><br/>
          <div className="box-1">
            <Input style={{"width" : "100%"}} placeholder='Email' modifier="material" float
              value={firebaseReducer.signUpUser.eMail} onChange={(evt) => actions.setAttribute({"attribute" : "SIGNUP_EMAIL", "value" : evt.target.value})} />
            <br/>
            <br/>
            <Input style={{"width" : "100%"}} placeholder='Password' modifier="material" float type="password"
              value={firebaseReducer.signUpUser.password} onChange={(evt) => actions.setAttribute({"attribute" : "SIGNUP_PASSWORD", "value" : evt.target.value})} />
            <br/>
            <br/>
            <br/>
            <Button  modifier='large' onClick={() => { actions.signUp(); }}  ripple={true}>Sign Up</Button>
          </div>
          <br/>
          <div className="link" onClick={() => actions.setAttribute({"attribute" : "LOGIN_SCREENMODE", "value" : "SIGNIN"})}>Sign In</div>
        </div>
      </div>}
    </div>
  );

  return (
    <Page renderToolbar={() => <NavBar backButton={true} title="Orchards" navigator={navigator} />}>
      <div style={styles.main}>
        {loginContent}
        {createAccountContent}
        {forgotPasswordContent}
        {logoutContent}
        {userProfile}
      </div>
    </Page>
  );
};








const mapStateToProps = (state) => ({
  ...state
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(Actions, dispatch)
  };
};

export default connect( mapStateToProps, mapDispatchToProps )(FirebasePage);



