import {SET_ATTRIBUTE, RECEIVE_SIGNUP, RECEIVE_SIGNIN, RECEIVE_SIGNOUT, RECEIVE_RESETPASSWORD} from '../actions';

let initialState = {newUser : {}, signInUser : {}, resetPassword : {}, signUpUser : {}, screenMode: "SIGNIN"};

const firebaseReducer = (state = initialState, action) => {
  switch (action.type) {
    case RECEIVE_SIGNUP:
      return {...state, screenMode :"SIGNEDIN"}
      break;
    case RECEIVE_RESETPASSWORD:
      return state
      break;
    case RECEIVE_SIGNIN:
      return {...state, screenMode :"SIGNEDIN"}
      break;
    case RECEIVE_SIGNOUT:
      return {...state, screenMode :"SIGNIN"}
      break;
    case SET_ATTRIBUTE:
      let currentNewUser = state.newUser;
      let currentSignInUser = state.signInUser;
      let currentSignUpUser = state.signUpUser;
      let currentResetPassword = state.resetPassword;
      console.log("setting attribute");
      switch (action.payload.attribute){
        case "LOGIN_SCREENMODE":
        return {...state, screenMode: action.payload.value};
        break;
        case "SIGNIN_EMAIL":
        currentSignInUser.eMail = action.payload.value;
        break;
        case "SIGNIN_PASSWORD":
        currentSignInUser.password = action.payload.value;
        break;
        case "SIGNUP_EMAIL":
        currentSignUpUser.eMail = action.payload.value;
        break;
        case "SIGNUP_PASSWORD":
        currentSignUpUser.password = action.payload.value;
        break;
        case "RESETPASSWORD_EMAIL":
        currentResetPassword.eMail = action.payload.value;
        break;
      }
      return { ...state, resetPassword: currentResetPassword, signInUser : currentSignInUser, signUpUser : currentSignUpUser };
    default:
      return state;
  }
};

export default firebaseReducer;



