import {SET_ATTRIBUTE} from '../actions';

let initialState = {settings : {}};

const kidSearchReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_ATTRIBUTE:
      switch (action.payload.attribute){
        case "KIDSEARCH_":
        return state;
        break;
      }
    default:
      return state;
  }
};

export default kidSearchReducer;



