import React, { Component, PropTypes } from 'react'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import {platform} from 'onsenui';

import * as Actions from '../actions';

import { Page, ProgressCircular, Button, Input } from 'react-onsenui';

import NavBar from '../components/NavBar';

const styles = {
  main: {
    fontFamily: platform.isIOS() ? 'Lato' : null,
    textAlign: 'center',
    color: '#4a4a4a',
    width: '100%',
    marginTop: '30px'
  },
  name: {
    textTransform: 'uppercase',
    fontSize: '24px',
    lineHeight: '24px'
  }
};

const PreferencesPage = ({
  navigator, preferencesReducer, actions
}) => {
  let preferencesContent;
  preferencesContent = (
    <div>
      <div style={styles.name}>
        <div style={{margin: '20px'}}>
          <h3>Settings</h3>
          <br/>
        </div>
      </div>
    </div>
  )

    let userProfile = (
      <div>
        {firebase.auth().currentUser && <h4>
          Welcome {JSON.stringify(firebase.auth().currentUser.email)}
        </h4>}
      </div>
    )


  return (
    <Page renderToolbar={() => <NavBar backButton={true} title="Preferences" navigator={navigator} />}>
      <div style={styles.main}>
        {userProfile}
        {preferencesContent}
      </div>
    </Page>
  );
};








const mapStateToProps = (state) => ({
  ...state
});

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(Actions, dispatch)
  };
};

export default connect( mapStateToProps, mapDispatchToProps )(PreferencesPage);



