import React from 'react';
import { Page, List, ListItem} from 'react-onsenui';

import NavBar from './NavBar';
import FirebasePage from '../containers/FirebasePage';
import PreferencesPage from '../containers/PreferencesPage';
import TemplatePage from '../containers/TemplatePage';
import HomePage from '../containers/HomePage';
import KidSearchPage from '../containers/KidSearchPage';
import IssueListPage from '../containers/IssueListPage';
import IssueDetailPage from '../containers/IssueDetailPage';
import ViolationListPage from '../containers/ViolationListPage';
import ViolationDetailPage from '../containers/ViolationDetailPage';
import TipListPage from '../containers/TipListPage';
import TipDetailPage from '../containers/TipDetailPage';
import AddMemberPage from '../containers/AddMemberPage';
import ProfilePage from '../containers/ProfilePage';

const MainPage = ({navigator}) => (
  <Page renderToolbar={() => <NavBar title='Orchards' navigator={navigator} />}>
    <List>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: FirebasePage});}} tappable>
        Firebase Login..
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: PreferencesPage});}} tappable>
        Preferences
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: HomePage});}} tappable>
        Home
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: KidSearchPage});}} tappable>
        KidSearch
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: IssueListPage});}} tappable>
        IssueList
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: IssueDetailPage});}} tappable>
        IssueDetail
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: ViolationListPage});}} tappable>
        ViolationList
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: ViolationDetailPage});}} tappable>
        ViolationDetail
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: TipListPage});}} tappable>
        TipList
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: TipDetailPage});}} tappable>
        TipDetail
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: AddMemberPage});}} tappable>
        AddMember
      </ListItem>
      <ListItem navigator={navigator} onClick={() => { navigator.pushPage({component: ProfilePage});}} tappable>
        Profile
      </ListItem>
      
    </List>
  </Page>
);

export default MainPage;


