import React from 'react';
import {render} from 'react-dom';
import {Provider} from 'react-redux';
import {createStore, applyMiddleware, compose} from 'redux';
import thunk from 'redux-thunk';
import createLogger from 'redux-logger';
import {AppContainer} from 'react-hot-loader';

import orchardApp from './reducers';
import App from './components/App';

import ons from 'onsenui';
import 'onsenui/css/onsenui.css';
import './stylus/index.styl';

import {setAttribute} from './actions';


const logger = createLogger();

const store = createStore(orchardApp,
  window.devToolsExtension ? window.devToolsExtension() : f => f,
  process.env.NODE_ENV === 'production'
    ? applyMiddleware(thunk)
    : applyMiddleware(thunk, logger)
);

fireDb.ref('violationRules').on('value',  function(snapshot) {
  let violationRules = [];
  let rules = snapshot.val();
   for(var prop in rules){
      if (typeof(rules[prop]) === 'object'){
        let vr = rules[prop];
        violationRules.push({"title" : vr.ruleTitle, "description" : vr.ruleDescription});
      }
   }
  store.dispatch(setAttribute({"attribute" : "VIOLATIONLIST_VIOLATIONRULES", "value" : violationRules}));
});

const rootElement = document.getElementById('root');

ons.ready(() => render(
  <AppContainer>
    <Provider store={store}>
      <App />
    </Provider>
  </AppContainer>,
  rootElement
));

if (module.hot) {
  module.hot.accept('./components/App', () => {
    const NextApp = require('./components/App').default;
    render(
      <AppContainer>
        <Provider store={store}>
          <NextApp />
        </Provider>
      </AppContainer>,
      rootElement
    );
  });
}



